<template>
  <div id="app">
    <Navbar />
    <div class="container">
      <router-view/>
    </div>
    <quick-menu backgroundColor ='#000000' :menu-count='4' :icon-class='["fa fa-address-book", "fa fa-home", "fa fa-line-chart", "fa fa-mail-reply"]' :menu-url-list='["#/cont","#","#","#/"]'></quick-menu>
  </div>
</template>

<script>
import Navbar from './components/Navbar'
import quickMenu from 'vue-quick-menu'
export default {
  name: "App",
  components: {
    Navbar,
    quickMenu
  }
};
</script>

<style>
  nav {
    margin-bottom:10px;
  }
</style>



