<template>
    <div>
      <ul class="collection with-header">
        <li class="collection-header"><h4>Contatos</h4></li>
        <li v-for="contato in contatos" v-bind:key="contato.id" class="collection-item">
        {{contato.primeiroNome}}
        <router-link class="secondary-content" v-bind:to="{name: 'view-contato', params: {contato_id: contato.contato_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
        </li>
      </ul>
      <div class="fixed-action-btn">
        <!-- botao com link para criação de novo contato -->
        <router-link to="/novo_cont" class="btn-floating">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
    </div>
</template>

<script>
import firebase from './firebaseInit'
export default {
  name: 'Contatos',
  data () {
    return {
      contatos: []
    }
  },
  created() {
    firebase.collection('contatos').orderBy('primeiroNome').get().then(
      querySnapshot => {
        querySnapshot.forEach(element => {
          console.log(element.data());
          console.log(element.id);
          const data = {
            'id' : element.id,
            'contato_id' : element.data().contato_id,
            'primeiroNome' : element.data().primeiroNome,
            'ultimoNome' : element.data().ultimoNome,
            'morada' : element.data().morada,
            'telemovel' : element.data().contacto,
          }
          this.contatos.push(data)
        }
        );
      }
    )
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



